import React, { Component } from 'react';
import { StyleSheet, css } from 'aphrodite';

import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import getMuiTheme from 'material-ui/styles/getMuiTheme';
import AppBar from 'material-ui/AppBar';
import $ from 'jquery';

import Boxes from '../components/boxes';
import Congratulations from '../components/congratulations';
import Question from '../components/question';
import TEDMenu from '../components/ted-menu.js';

const questions = 	[ 	{question:"answer:1234", keyword:"1234", img:"/resources/image.png", },
						{question:"answer:2345", keyword:"2345", img:"/resources/image.png", },
						{question:"answer:3456", keyword:"3456", img:"/resources/image.png", },
						{question:"answer:4567", keyword:"4567", img:"/resources/image.png", },
						{question:"answer:5678", keyword:"5678", img:"/resources/image.png", },
						{question:"answer:6789", keyword:"6789", img:"/resources/image.png", }
					];
					
const cacheKey = 'index';

class Scavenger extends Component {
    constructor() {
        super();
		
		var i = 0;
		if (localStorage[cacheKey] !== undefined){
			i = parseInt(localStorage[cacheKey]);
		}
		
        this.state = {
            open: false,
			correct: false,
			index: i,
        };
        
        this.openMenu = this.openMenu.bind(this);
        this.closeMenu = this.closeMenu.bind(this);
		this.increment = this.increment.bind(this);
		this.incorrect = this.incorrect.bind(this);
		this.correct = this.correct.bind(this);
		this.notification = this.notification.bind(this);
    }
	
	componentDidMount() {
		$("#curtain").animate({
			opacity:0,
		}, 1);
		$("#notification").animate({
			opacity:0,
		}, 1);
	}
    
	openMenu() {
		this.setState({ open: true });
	};

	closeMenu() {
		this.setState({ open: false });
	};
	
	incorrect() {
		this.notification();
		this.setState({ correct: false });
	}
	
	correct() {
		this.setState({ correct: true });
		this.notification();
	}
	
	increment() {
		localStorage[cacheKey] = '' + (this.state.index+1);
		this.setState({ index: this.state.index+1 });
	};
	
	notification() {
		
		$("#curtain").animate({
			opacity:1,
		}, {duration:250, queue:false});
		$("#notification").animate({
			opacity:1,
		}, {duration:250, queue:false});
		
		$("#curtain").animate({
			height:'0%',
		}, {duration:250, queue:false, complete:function() {
			$("#curtain").animate({
				opacity:'1'
			}, 4000, function() {
				$("#curtain").animate({
					height:'100%',
				}, 250, function() {
					$("#curtain").animate({
						opacity:0,
					}, 1);
					$("#notification").animate({
						opacity:0,
					}, 1);
				});
				
			});
		}});
	}
    
	render() {
		
		const goodJobMessage = "Good job, that's correct!";
		const goodJobSubMsg = "Time for the next clue.";
		const sorryMessage = "Sorry, that's incorrect.";
		const sorrySubMsg = "Please try again!";
		
		var content = null;
		
		if (this.state.index < questions.length) {
			var question = questions[this.state.index];
			content = (
				<div>
					<Question question={question.question} keyword={question.keyword} img={question.img}
									next={this.correct} incorrect={this.incorrect} 
									increment={this.increment}/>
					<Boxes total={questions.length} index={this.state.index} />
					
					<div className={css(styles.container)} id="notification" style={{height:'15vh',}}>
						<div className={css(styles.textcontainer)}>
							<p className={css(styles.line1)}> {this.state.correct ? goodJobMessage : sorryMessage} </p>
							<p className={css(styles.line2)}> {this.state.correct ? goodJobSubMsg : sorrySubMsg} </p>
						</div>
					</div>
					
					<div className={css(styles.curtain)} id="curtain" style={{height:'15vh',}}/>
				</div>);
		} else {
			content = (
				<div>
					<Congratulations />
					<Boxes total={questions.length} index={this.state.index} />
				</div>);
		}
		
		return (

			<MuiThemeProvider muiTheme={muiTheme}>
                <div>
                    
                    <AppBar 
                        title="TEDxUofW 2018"
                        titleStyle={{
                            fontSize: '16px',
                            fontWeight:'bold',
                            textAlign: 'center'
                        }} 
                        iconClassNameRight="muidocs-icon-navigation-expand-more" 
                        onLeftIconButtonClick={this.openMenu}
                    />
					{content}
					
                    <TEDMenu open={this.state.open} close={this.closeMenu} />
                </div>
			</MuiThemeProvider>

		);
	}
}

const styles = StyleSheet.create({
	curtain: {
		position:'absolute',
		marginTop:'67vh',
		width:'100vw',
		backgroundColor:'rgba(255, 255, 255, 1)'
	},
	container: {
		position:'absolute',
		marginTop:'67vh',
		width:'100vw',
		textAlign:'left',
		backgroundColor:'rgba(230, 43, 37, 1)'
	},
	textcontainer: {
		position:'relative',
		marginTop:'5vh',
		marginLeft:'20vw',
	},
	line1: {
		position:'relative',
		fontSize:'15px',
		fontFamily:'AvenirBlack',
		marginBottom:'0px',
		color:'white',
	},
	line2: {
		position:'relative',
		fontSize:'15px',
		fontFamily:'Avenir',
		marginTop:'0px',
		color:'white',
	}
});

const muiTheme = getMuiTheme({
    palette: {
        textColor: '#000'
    },
    appBar: {
        color: 'rgba(230, 43, 37, 1)',
    },
});

export default Scavenger;


